import React, { Component } from "react";
import { connect } from "react-redux";

export class SignIn extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showImage: false,
			imageSource: "",
		};
	}

	openImage = () => {
		console.log("clicked");
		var pick = navigator.MozActivity({
			name: "pick",
			data: {
				type: ["image/png", "image/jpg", "image/jpeg"],
				// In FxOS 1.3 and before the user is allowed to crop the
				// image by default, but this can cause out-of-memory issues
				// so we explicitly disable it.
				nocrop: true, // don't allow the user to crop the image
			},
		});
		console.log(pick);

		pick.onsuccess = function () {
			var img = document.createElement("img");
			img.src = window.URL.createObjectURL(this.result.blob);
			var imagePresenter = document.querySelector("#image-presenter");
			imagePresenter.appendChild(img);
			imagePresenter.style.display = "block";
		};

		pick.onerror = function () {
			console.log("Can't view the image");
		};
	};

	render() {
		return (
			<div style={{ textAlign: "center" }}>
				<h4 style={{ fontWeight: "bold" }}>Sign In Screen</h4>
				<button className="btn-red" id="pick-image">
					wifi
				</button>
				<div id="image-presenter"></div>
			</div>
		);
	}
}

const mapStateToProps = (state) => ({});

export default connect(mapStateToProps)(SignIn);
